import * as React from "react";
import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";
import * as styles from "./frame-component2.module.css";

type FrameComponent2Type = {
  gMT0000UTC?: string;

  /** Style props */
  frame62AccentColor?: Property.AccentColor;
};

const FrameComponent2: FunctionComponent<FrameComponent2Type> = ({
  gMT0000UTC,
  frame62AccentColor,
}) => {
  const frameDivStyle: CSS.Properties = useMemo(() => {
    return {
      accentColor: frame62AccentColor,
    };
  }, [frame62AccentColor]);

  return (
    <div className={styles.gmt0000UtcParent} style={frameDivStyle}>
      <div className={styles.gmt0000Utc}>{gMT0000UTC}</div>
      <img className={styles.chevronDownIcon} alt="" src="/chevrondown.svg" />
    </div>
  );
};

export default FrameComponent2;
